import React, { Component } from 'react';
import {
  VerticalTimeline,
  VerticalTimelineElement
} from 'react-vertical-timeline-component';

import { BrowserView, MobileView } from 'react-device-detect';

import 'react-vertical-timeline-component/style.min.css';
import { Typography } from '@material-ui/core';
import ministeries from '../data/ministeries';
import Moment from 'react-moment';

import 'moment/locale/es';

function Year(props) {
  const date = props.children;
  return (
    <VerticalTimelineElement
      iconStyle={{ background: '#14aa9c', color: '#fff' }}
      icon={
        <div className='year-container'>
          <BrowserView>
            <Typography variant='h6'>{date}</Typography>
          </BrowserView>
          <MobileView>
            <Typography variant='h6'>{date % 100}</Typography>
          </MobileView>
        </div>
      }
    />
  );
}

// https://www.youtube.com/embed/IBi3d7aFL54
function Youtube(props) {
  // eslint-disable-next-line
  return (
    <iframe
      width='100%'
      height='230'
      src={props.link}
      frameBorder='0'
      allow='accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture'
      allowFullScreen
    ></iframe>
  );
}

class Preach extends Component {
  componentDidMount() {
    const { id, type } = this.props.children;
    const { color } = ministeries[type];
    document.getElementById(
      'timeline-' + id
    ).childNodes[0].childNodes[1].style.borderTop = '3px solid ' + color;
  }

  render() {
    const {
      id,
      name,
      preacher,
      link,
      caption,
      type,
      date
    } = this.props.children;
    const { color, defaultPreacher, icon } = ministeries[type];
    return (
      <VerticalTimelineElement
        id={'timeline-' + id}
        date={<Moment format='MMMM DD, YYYY'>{date}</Moment>}
        iconStyle={{ background: color, color: '#fff' }}
        icon={icon}
      >
        <div className='timeline-date'>
          <Typography variant='caption'>
            <Moment fromNow>{date}</Moment>
          </Typography>
        </div>
        <Typography className='vertical-timeline-element-title' variant='h5'>
          {name}
        </Typography>
        <Typography
          className='vertical-timeline-element-subtitle'
          variant='subtitle1'
        >
          {preacher ? preacher : defaultPreacher}
        </Typography>
        <br />
        <Youtube link={link} />
        <Typography variant='caption'>{caption}</Typography>
        <br />
      </VerticalTimelineElement>
    );
  }
}

function Article(props) {
  return <></>;
}

class Timeline extends Component {
  state = { renders: [] };

  componentDidMount() {
    let result = [];
    let tmpYear = new Date().getFullYear();
    let { events } = this.props;

    events.sort((a, b) => {
      const dateA = Date.parse(a.date);
      const dateB = Date.parse(b.date);
      return dateA === dateB ? 0 : dateA > dateB ? -1 : 1;
    });

    for (let element of events) {
      const year = new Date(Date.parse(element.date)).getFullYear();
      if (year !== tmpYear) {
        for (; year !== tmpYear; tmpYear--) {
          result.push(<Year key={tmpYear}>{tmpYear}</Year>);
        }
      }
      result.push(element.item);
    }
    result.push(<Year key={tmpYear}>{tmpYear}</Year>);

    this.setState({ renders: result });
  }

  render() {
    const { renders } = this.state;

    return <VerticalTimeline>{renders}</VerticalTimeline>;
  }
}

export { Timeline, Preach, Article };
