import React, { Component } from 'react'
import { Timeline, Preach } from '../components/Timeline';

import events from '../data/events.json';

class Home extends Component {
  render() {
    return (
      <Timeline events={events.map(item => ({
        item: <Preach key={item.id}>{item}</Preach>,
        date: item.date, 
      }))}/>
    )
  }
}

export default Home;
